export default {
  name: 'ourGoTop',
  data () {
    return {
      timer: null,
      isShow: false
    }
  },
  props: {
    width: {
      type: Number,
      default: 50
    },
    height: {
      type: Number,
      default: 50
    },
		
    right: {
      type: Number,
      default: 30
    },
    bottom: {
      type: Number,
      default: 50
    },
    zIndex: {
      type: Number,
      default: 1000
    },
    color: {
      type: String,
      default: '#333'
    },
    scrollY: {
      type: Number,
      default: 1000
    },
    scrollSpeed: {
      type: Number,
      default: 100
    },
		duration: {
			type: Number,
			default: 300
		}
  },
  mounted () {

		this.isShow = true

		



  },
  beforeDestroy () {



  },
  methods: {

			goTop () {
				uni.pageScrollTo({
					scrollTop: 0,
					duration: this.duration
				});
			}

		
























  }
}
